import {
  SUCCESS_CATEGORIES,
  FAILURE_CATEGORIES,
  SUCCESS_CATEGORY,
  FAILURE_CATEGORY,
  SUCCESS_SUB_CATEGORY,
  FAILURE_SUB_CATEGORY
} from "../constants/menu";

const inititalState = {
  categoriesNav: [],
  category: {},
  subCategory: [],
  errorCategories: "",
  errorCategory: "",
  errorSubCategory: ""
};

export default function(state = inititalState, action) {
  switch (action.type) {
    case SUCCESS_CATEGORIES:
      return { ...state, categoriesNav: action.payload };
    case FAILURE_CATEGORIES:
      return { ...state, errorCategories: action.payload };
    case SUCCESS_CATEGORY:
      return { ...state, category: action.payload };
    case FAILURE_CATEGORY:
      return { ...state, errorCategory: action.payload };
    case SUCCESS_SUB_CATEGORY:
      return { ...state, subCategory: action.payload };
    case FAILURE_SUB_CATEGORY:
      return { ...state, errorSubCategory: action.payload };
    default:
      return state;
  }
}



