import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import MiniNavMenu from "../components/Elements/miniNavMenu/MiniNavMenu";
import NavMenu from "../components/Category/navMenu/NavMenu";
import ProductList from "../components/Category/productList/ProductList";
import * as categoriesActions from "../actions/categoriesActions";

class Category extends Component {
  componentWillMount() {
    this.props.categoriesActions.getCategories();
    if (this.props.type === "category") {
      this.props.categoriesActions.getCategory(this.props.match.params.id);
    }
    if (this.props.type === "childCategory") {
      this.props.categoriesActions.getSubCategory(this.props.match.params.id);
    }
  }
  componentDidUpdate(prevProps) {
    if (
      this.props.type === "category" &&
      this.props.match.params.id !== prevProps.match.params.id
    ) {
      this.props.categoriesActions.getCategory(this.props.match.params.id);
    }
    if (
      this.props.type === "childCategory" &&
      this.props.match.params.id !== prevProps.match.params.id
    ) {
      this.props.categoriesActions.getSubCategory(this.props.match.params.id);
    }
  }

  render() {
    const { categoriesNav, category } = this.props.categories;
    return (
      <div className="fluid-container">
        <div className="row">
          <MiniNavMenu
            type={this.props.type}
            idActiveCategory={this.props.match.params.id}
            categoryNav={category}
          />
        </div>
        <div className="row">
          <NavMenu
            categories={categoriesNav}
            idActiveCategory={this.props.match.params.id}
            type={this.props.type}
          />
          <ProductList
            category={category}
            idActiveCategory={this.props.match.params.id}
          />
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    categories: state.categories
  };
}

function mapDispatchToProps(dispatch) {
  return {
    categoriesActions: bindActionCreators(categoriesActions, dispatch)
  };
}

Category.propTypes = {
  categories: PropTypes.shape({
    categoriesNav: PropTypes.array,
    category: PropTypes.object
  }).isRequired,
  categoriesActions: PropTypes.shape({
    getCategories: PropTypes.func,
    getCategory: PropTypes.func,
    getSubCategory: PropTypes.func
  }).isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      id: PropTypes.string
    })
  }).isRequired,
  type: PropTypes.string.isRequired
};

export default connect(mapStateToProps, mapDispatchToProps)(Category);



