import React from "react";
import styles from "./productList.css";
import ProductTile from "../../Elements/productTile/ProductTile";

const ProductList = state => {
  const productsBlock = (state.category.products || [])
    .map(product => <ProductTile product={product} key={product.id} />);

  return (
    <div className="col-xl-9 col-lg-9 col-md-9">
      <div className={styles.productList}>
        <h2>
          {state.category.name}
          <span className={styles.counterProduct}>
            {state.category.count} 
          </span>{" "}
        </h2>
        {productsBlock}
      </div>
    </div>
  );
};
export default ProductList;



