import React from "react";
import styles from "./infoBox.css";

const InfoBox = state => (
  <div className={styles.infoBox}>
    <img src={state.icon} alt={state.text.heading} />
    <h3>{state.text.heading}</h3>
    <div>
      <p>{state.text.content}</p>
    </div>
  </div>
);

export default InfoBox;



