import React from 'react';
import styles from './headingBar.css';

const HeadingBar = state => (
  <div className={styles.headingBlock}>
    <img src={state.icon} alt={state.alt} />
    <h2>{state.heading}</h2>
  </div>
);

export default HeadingBar;



