import React from "react";
import { Link } from "react-router-dom";
import { transliterate } from "../../../global/transliterate";
import styles from "./navMenu.css";

const NavMenu = state => {
  const activeCategory = id => {
    if (state.type === "category") {
      return id === JSON.parse(state.idActiveCategory) ? styles.active : "";
    }
    return false;
  };
  const activeArrowCategory = id => {
    if (state.type === "category") {
      return id === JSON.parse(state.idActiveCategory)
        ? styles.arrow
        : styles.arrowDisable;
    }
    return false;
  };
  const activeSubCategory = id => {
    if (state.type === "childCategory") {
      return id === JSON.parse(state.idActiveCategory) ? styles.active : "";
    }
    return false;
  };
  const activeSubArrowCategory = id => {
    if (state.type === "childCategory") {
      return id === JSON.parse(state.idActiveCategory)
        ? styles.arrow
        : styles.arrowDisable;
    }
    return false;
  };
  let childCategories;
  const categories = (state.categories || []).map(category => (
    <div key={category.id}>
      <Link
        to={`/category/${transliterate(category.name)}/${category.id}`}
        className={activeCategory(category.id)}
      >
        <div className={activeArrowCategory(category.id)} />
        {category.name}
        <p>{category.count}</p>
      </Link>
      {
        (childCategories = (category.child_categories || [])
          .map(childCategory => (
            <Link
              to={`/subcategory/${transliterate(
                childCategory.name
              )}/${childCategory.id}`}
              className={`${styles.childCategory} ${activeSubCategory(
                childCategory.id
              )}`}
              key={childCategory.id}
            >
              <div className={activeSubArrowCategory(childCategory.id)} />
              {childCategory.name}
              <p>{childCategory.count}</p>
            </Link>
          )))
      }
    </div>
  ));

  return (
    <div className="col-xl-3 col-lg-3 col-md-3">
      <div className={styles.navMenu}>
        <h2></h2>
        {categories}
      </div>
    </div>
  );
};

export default NavMenu;



