import React, { Component } from "react";
import { Link } from "react-router-dom";
import Select from "react-select";
import PropTypes from "prop-types";
import styles from "./productTile.css";
import testLogo from "./testLogo.png";

export default class ProductTile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      price: this.props.product.price,
      salePrice: this.props.product.sale_price,
      status: this.props.product.status
    };
    this.changeProduct = this.changeProduct.bind(this);
  }

  changeProduct(event) {
    const eventValue = event.target.value;
    this.setState(() => ({ price: eventValue }));
  }

  render() {
    const options = (this.props.product.child_products || [])
      .map(childProduct => (
        {value: childProduct.price ,label: childProduct.name}
      ));

    const colors = (this.props.product.colors || []).map((color, index) => {
      if (index === 0) {
        return (
          <label
            htmlFor={color.id}
            className={styles.customRadio}
            key={color.id}
          >
            <input id={color.id} name="test" type="radio" defaultChecked />
            <span style={{ backgroundColor: `${color.hex}` }} />
          </label>
        );
      }
      return (
        <label htmlFor={color.id} className={styles.customRadio} key={color.id}>
          <input id={color.id} name="test" type="radio" />
          <span style={{ backgroundColor: `${color.hex}` }} />
        </label>
      );
    });

    return (
      <div className={styles.productTile}>
        <Link to="/">
          <img src={JSON.parse(this.props.product.images)} alt="" />
        </Link>
        <form>
          <img src={testLogo} alt="" />
          <Link to="/">
            <h3>{this.props.product.name}</h3>
          </Link>
          <Select
            name=" "
            onChange={this.changeProduct}
            value="one"
            Searchable="false"
            options={options}
            style={{
              display:
                this.props.product.child_products.length <= 1 ? "none" : "block"
            }}
          />
          <p>{this.props.product.seo_description}</p>
          <div className={styles.bottomElement}>
            <div
              className={styles.colorChoice}
              style={{
                display:
                  this.props.product.colors.length <= 1
                    ? "none"
                    : "inline-block"
              }}
            >
              <p></p>
              {colors}
            </div>
            <p
              className={
                this.state.status === "sale" ? styles.sale : styles.price
              }
            >
              <span>{this.state.salePrice} </span>
              {this.state.price} 
            </p>
            <button type="submit"></button>
          </div>
        </form>
      </div>
    );
  }
}

ProductTile.propTypes = {
  product: PropTypes.shape({
    child_products: PropTypes.array,
    colors: PropTypes.array,
    price: PropTypes.number,
    sale_price: PropTypes.number,
    status: PropTypes.string,
    images: PropTypes.string,
    name: PropTypes.string,
    seo_description: PropTypes.string
  }).isRequired
};



