import React from 'react';
import { Link } from 'react-router-dom';
import { transliterate } from '../../../global/transliterate';
import styles from './mobileMinCategory.css';

const MobileMinCategory = state => {
  const categories = (state.categories || []).map(category => (
    <Link
      to={`/category/${transliterate(category.name)}/${category.id}`}
      key={category.id}
    >
      {category.name}
      <p>&gt;</p>
    </Link>
  ));
  return <div className={styles.category}>{categories}</div>;
};

export default MobileMinCategory;



