import axios from "axios";
import {
  REQUEST_CATEGORIES,
  SUCCESS_CATEGORIES,
  FAILURE_CATEGORIES,
  REQUEST_CATEGORY,
  FAILURE_CATEGORY,
  SUCCESS_CATEGORY,
  REQUEST_SUB_CATEGORY,
  SUCCESS_SUB_CATEGORY,
  FAILURE_SUB_CATEGORY
} from "../constants/menu";

export function getCategories() {
  return (dispatch) => {
    dispatch({
      type: REQUEST_CATEGORIES
    });
    axios({
      method: "get",
      /* url: '/api/getCategories' */
      url: "http://laravel.app/api/getCategories"
    })
      .then(response => {
        dispatch({
          type: SUCCESS_CATEGORIES,
          payload: response.data
        });
      })
      .catch(error => {
        dispatch({
          type: FAILURE_CATEGORIES,
          payload: error.message
        });
      });
  };
}
export function getCategory(id) {
  return (dispatch) => {
    dispatch({
      type: REQUEST_CATEGORY
    });
    axios({
      method: "get",
      /* url: `/api/getCategory/${id}` */
      url: `http://laravel.app/api/getCategory/${id}`
    })
      .then(response => {
        dispatch({
          type: SUCCESS_CATEGORY,
          payload: response.data
        });
      })
      .catch(error => {
        dispatch({
          type: FAILURE_CATEGORY,
          payload: error.message
        });
      });
  };
}
export function getSubCategory(id) {
  return (dispatch) => {
    dispatch({
      type: REQUEST_SUB_CATEGORY
    });
    axios({
      method: "get",
      /* url: `/api/getChildCategory/${id}` */
      url: `http://laravel.app/api/getChildCategory/${id}`
    })
      .then(response => {
        dispatch({
          type: SUCCESS_SUB_CATEGORY,
          payload: response.data
        });
      })
      .catch(error => {
        dispatch({
          type: FAILURE_SUB_CATEGORY,
          payload: error.message
        });
      });
  };
}



